//480px, 768px, 1025px, 1200px
$min-width-breakpoints: (
  'mobile': '25em',
  'tablet': '48em',
  'laptop': '64em',
  'desktop': '75.5em',
);

@mixin min-width-breakpoints($size) {
  @media (width > map-get($min-width-breakpoints, $size)) {
    @content;
  }
  // @media only screen and (min-width: map-get($min-width-breakpoints, $size)) {
  //   @content;
  // }
}
