@media (width > 64em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

.header {
  background-image: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url(/img/hero.jpg);
  background-size: cover;
}
.header__text {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-block: 15%;
}
.header__text h1 {
  font-size: clamp(3rem, 7vw, 8rem);
  text-transform: uppercase;
  transition: all 250ms ease;
  margin: 0;
}
.header__text p {
  max-width: 52rem;
  font-size: clamp(1.6rem, 2vw, 2rem);
}